import FeaturePageHeader from '../feature-page-header/FeaturePageHeader'
import { OPEN_WEB_PAGE_FEATURE } from '../../constants'
import { useState } from 'react'
import './OpenWebPage.scss'

export default function OpenWebPage() {
  const [link, setLink] = useState('https://www.tutorialrepublic.com/')


  const handleChange = (event: any) => {
    const { value } = event.target
    if (value) setLink(value)
  }

  return (
    <>
      <div className='feature-page'>
        <FeaturePageHeader name={OPEN_WEB_PAGE_FEATURE.name} />
        <div className='open-web-page'>
          <input
            className='open-web-page__input'
            type='url'
            defaultValue={link}
            onChange={handleChange}
          />
          <a className='open-web-page__link' href={link}>
            {link}
          </a>
        </div>
      </div>
    </>
  )
}
