import React, { useLayoutEffect } from 'react'
import './RequestGeolocation.scss'
import { useDispatch, useSelector } from 'react-redux'
import { getGeolocation } from '../../redux/selectors/client'
import FeaturePageHeader from '../feature-page-header/FeaturePageHeader'
import { REQUEST_GEOLOCATION } from '../../constants'
import { requestGeolocation, setGeolocation } from '../../redux/actions/client'
import { dateFormat } from '../../helpers'

function RequestGeolocation() {
  const dispatch = useDispatch()
  const geolocation = useSelector(getGeolocation)
  useLayoutEffect(() => {
    dispatch(setGeolocation(null))
  }, [dispatch])

  const handleSubmit = () => {
    dispatch(requestGeolocation())
  }

  return (
    <div className="feature-page">
      <FeaturePageHeader name={REQUEST_GEOLOCATION.name} />
      <div className="request-geolocation">
        <p className="request-geolocation__title"></p>
        <div className="request-geolocation__info">
          <div className="request-geolocation__info--latitude-longitude">{geolocation?.latitude}</div>
          <div className="request-geolocation__info--latitude-longitude">{geolocation?.longitude}</div>
          <button onClick={handleSubmit} className="request-geolocation__info--button">
            
          </button>
        </div>
        <div className="request-geolocation__timestamp">{dateFormat(geolocation?.timestamp)}</div>
      </div>
    </div>
  )
}

export default RequestGeolocation
